  background-color: #0000;
  width: 22px;
  height: 11px;
  display: block;
  position: relative;
}

.mobile-menu {
  z-index: 9;
}

.menu-btn:before, .menu-btn:after {
  content: "";
}

.menu-btn:before, .menu-btn__line, .menu-btn:after {
  background-color: #fffc;
  width: 100%;
  height: 2px;
  transition: top .5s linear, transform .5s linear;
  position: absolute;
  left: 0;
}

.menu-btn:before {
  top: 0;
}

.menu-btn__line {
  top: calc(50% - 1px);
}

.menu-btn:after {
  top: calc(100% - 2px);
}

.menu-btn.is-open:before {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}

.menu-btn.is-open .menu-btn__line {
  transform-origin: 0;
  transform: scaleX(0);
}

.menu-btn.is-open:after {
  top: calc(50% - 1px);
  transform: rotate(45deg);
}

@media (width <= 1023px) {
  .mobile-menu__wrapp {
    background-color: #191919;
