}

body {
  margin: 0;
}

.feedback {
  color: #fff;
  font-family: Arial, sans-serif;
}

.feedback__hero {
  width: 100%;
  height: 250px;
  overflow: hidden;
}

.feedback__hero-img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.feedback__inner {
  grid-template-columns: 1fr;
  gap: 50px;
  padding: 60px 0;
  display: grid;
}

.feedback__title {
  text-transform: uppercase;
  letter-spacing: 1px;
  margin: 0 0 20px;
  font-size: 28px;
  font-weight: 700;
}

.feedback__lead {
  color: #ccc;
  max-width: 400px;
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
}

.feedback__form {
  width: 100%;
}

.feedback__list {
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.feedback__item {
  border-bottom: 1px solid #333;
  padding: 20px 0;
}

.feedback__label-text {
  color: #666;
  margin-bottom: 8px;
  font-size: 14px;
  display: block;
}

.feedback__input, .feedback__textarea {
  color: #fff;
  background: none;
  border: none;
  width: 100%;
  padding: 0;
  font-family: inherit;
  font-size: 16px;
}

.feedback__input:focus, .feedback__textarea:focus {
  outline: none;
}

.feedback__textarea {
  resize: vertical;
  min-height: 60px;
}

.feedback__checkbox {
  color: #ccc;
  cursor: pointer;
  align-items: center;
  gap: 12px;
  margin-top: 25px;
  font-size: 12px;
  display: flex;
}

.feedback__link {
  color: #ccc;
  text-decoration: underline;
}

.feedback__btn {
  color: #fff;
  letter-spacing: 1px;
  cursor: pointer;
  text-transform: uppercase;
  background: none;
  border: 1px solid #5a1a1a;
  width: 100%;
  margin-top: 40px;
  padding: 18px;
  font-size: 13px;
  font-weight: bold;
  transition: all .3s;
}

.feedback__btn:hover {
  background: #5a1a1a;
  border-color: #5a1a1a;
}

@media (width >= 900px) {
  .feedback__hero {
    height: 400px;
  }

